import { Component, OnInit, QueryList, ViewChildren, ViewChild, Inject, HostListener } from '@angular/core';
import { Http, Response } from '@angular/http';
import { TranslateService } from "ng2-translate";
import { DOCUMENT } from '@angular/platform-browser';
import { MdSnackBar } from '@angular/material';
import 'rxjs/add/operator/toPromise';
import { ConfigService } from '../config.service';
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})

export class ListComponent implements OnInit {
  configFile: string = './assets/config.json?nocache=' + (new Date()).getTime();
  selectLang: string;
  id = 'a0';
  langSet: any = [];
  data: any = [];
  themes: any = [];
  anchors: any = [];
  defaultCategorys: any = [];
  category: any = [];
  selectedValue: string;
  searchValue: string = "";
  showSearch: boolean = false;

  constructor(private http: Http,
    @Inject(DOCUMENT) private document,
    private translate: TranslateService,
    private coreService: ConfigService,
    public snackBar: MdSnackBar) {

    const response = this.coreService.getConfig();
    this.langSet = response.language;
    if (localStorage.getItem('lang') === null) {
      // let userLang = navigator.language.split('-')[0];
      // userLang = /(zh|en)/gi.test(userLang) ? userLang : 'en';
      localStorage.setItem('lang', this.langSet[0].value);
      this.translate.setDefaultLang(this.langSet[0].value);
    }
    this.selectLang = localStorage.getItem('lang');
    this.translate.use(localStorage.getItem('lang'));
  }

  ngOnInit() {
    this.http.get(this.configFile)
      .toPromise()
      .then(response => {
        this.data = response.json();
        this.themes = this.data.theme;
        this.category = this.data.category;
        this.initTheme();
        this.initCards();
        this.defaultCategorys = JSON.parse(JSON.stringify(this.category));
      })
      .catch(this.handleError);
  }

  @HostListener("window:scroll", [])
  onWindowScroll() {
    let number = this.document.body.scrollTop;
    if (!this.anchors.length) {
      for (let i = 0, max = this.category.length; i < max; i++) {
        let id = '#a' + i;
        this.anchors.push(this.document.querySelector(id).offsetTop - 45);
      }
    }
    for (let i = 0, max = this.anchors.length; i < max; i++) {
      if (number < this.anchors[i]) {
        let n = i - 1;
        this.id = "a" + n;
        break;
      }
    }
  }

  setAnchor(index) {
    debugger;
    setTimeout(() => {
      this.id = "a" + index;
    }, 250);
  }

  changeLang() {
    localStorage.setItem('lang', this.selectLang);
    this.translate.use(this.selectLang);
  }

  initTheme() {
    let theme = localStorage.getItem('theme');
    if (theme) {
      this.selectedValue = theme;

    } else {
      this.selectedValue = this.themes[0];
    }
    this.getTheme(this.selectedValue);
  }

  initCards() {
    for (let i = 0, max = this.category.length; i < max; i++) {
      for (let j = 0, maxj = this.category[i].cards.length; j < maxj; j++) {
        this.category[i].cards[j].show = true;
      }
    }
  }

  onSearch() {
    if (!this.searchValue) {
      this.category = JSON.parse(JSON.stringify(this.defaultCategorys));
    } else {

      for (let i = 0, max = this.defaultCategorys.length; i < max; i++) {
        let tmp = [];
        for (let j = 0, maxj = this.defaultCategorys[i].cards.length; j < maxj; j++) {
          if (this.defaultCategorys[i].cards[j].keywords.indexOf(this.searchValue) !== -1) {
            let t = JSON.parse(JSON.stringify(this.defaultCategorys[i].cards[j]));
            t.show = true;
            tmp.push(t);
          }
        }
        this.category[i].cards = tmp;
      }
    }
  }

  getTheme(theme) {
    if (!theme) {
      theme = this.selectedValue
    }
    if (this.themes.indexOf(theme) === -1) {
      theme = this.themes[0];
      this.selectedValue = this.themes[0];
    }
    let path = './assets/css/theme/' + theme + '.css?' + (new Date()).getTime();;
    this.document.getElementById('theme').setAttribute('href', path);
    this.setStylePref(theme);
  }

  setStylePref(theme) {
    localStorage.setItem('theme', theme);
  }

  openUrl(url) {
    if (!url) {
      var x = document.getElementById("snackbar")
      x.className = "show";
      setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
    } else {
      window.open(url);
    }
  }

  private handleError(error: any): Promise<any> {
    console.error('[config] error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

}



